import React from "react";

const styles = {
    container3: {
        textAlign: "center",
        backgroundColor: "black",
    },
    footer: {
        position: "fixed",
        left: "0",
        bottom: "0",
        width: "100%",
        color: "white",
        textAlign: "center",
        backgroundColor: "black",
    },
};

function Footer() {
    return (
        <div style={styles.container3} className="container-fluid">
            <div className="row">
                <div className="col-12">
                    <footer style={styles.footer}>
                        <p style={{ color: "white" }}>
                            &copy; 2020 Reynholm Industries.
                        </p>
                    </footer>
                </div>
            </div>
        </div>
    );
}

export default Footer;
