import React from "react";

const styles = {
    container2: {
        borderTop: "35px, solid, lightgray",
        borderBottom: "35px, solid, lightgray",
        borderRight: "15px, solid, lightgrey",
        borderLeft: "35px, solid, lightgrey",
        color: "green",
    },
    AboutMe: {
        backgroundColor: "lightgray",
        display: "inline-block",
        fontSize: "25px",
        alignContent: "center",
        padding: "0%",
        alignSelf: "center",
        borderLeft: "gray",
        boxSizing: "border-box",
    },
    filler: {
        backgroundColor: "lightgray",
    },
};

function Contact() {
    return (
        <div style={styles.container2} className="container-fluid">
            <div className="row">
                <div className="col-8">
                    <h1 style={styles.AboutMe}>Contact Jim Denis</h1>
                </div>
                <div style={styles.filler} className="col-4"></div>
                <div className="col-8">
                    <hr />
                </div>
                <div style={styles.filler} className="col-4"></div>
                <div className="col-8 ">
                    <form action="/action_page.php">
                        <div className="form-group">
                            <label htmlFor="name">Name:</label>
                            <input
                                type="name"
                                className="form-control"
                                id="name"
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="email">Email:</label>
                            <input
                                type="email"
                                className="form-control"
                                id="email"
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="comment">Message:</label>
                            <textarea
                                className="form-control"
                                rows="5"
                                id="comment"
                            ></textarea>
                        </div>
                        <button type="submit" className="btn btn-default">
                            Submit
                        </button>
                    </form>
                </div>
                <div style={styles.filler} className="col-4"></div>
            </div>
        </div>
    );
}

export default Contact;
